import React, { useState } from "react";
import "bootstrap/dist/css/bootstrap.min.css"; // Import Bootstrap CSS
import { googleDmTool } from "./AiGen";
import Quest from "./Quest";

const FourTextInputs = ({ apiKey }) => {
  const [formData, setFormData] = useState({
    input1: "",
    input2: "",
    input3: "",
    input4: "",
  });
  const [loading, setLoading] = useState(false);
  const [quest, setQuest] = useState(false);
  const [adventureData, setAdventureData] = useState({});
  const [adventureKey, setAdventureKeys] = useState([]);

  const handleChange = (event) => {
    const { name, value } = event.target;
    setFormData({ ...formData, [name]: value });
  };

  const handleSubmit = (event) => {
    event.preventDefault(); // Prevent default form submission behavior
    console.log("Submitted form data:", formData);
    setLoading(true);
    const prompt = `Reply in pure json format. In this format {message:string,quests:[{description:string,title:string, options:[reward:{type:''description:'',amount:int},fail:{},description:'',pass:int,skill]}]}. You are the gamemaster please form your answer to the following question with these details. The setting is ${formData.input1} with a name. The over all goal is ${formData.input2}. Please generate ${formData.input4} quests that end with the last quest facing the big bad ${formData.input3}. Give the big bad a name. Please give the character 4 options for interacting with the quests. Each option should have an action and a description in an array. Each option should have a dungeons and dragons skill and no value. Each option should have a 'pass' value between 5 and 15. Each option should have a 'xp' value between 10 and 50. reward should not be 'null'  a 'reward' description as a string is the player is successfull this can be one of the following types as an object; information, an item with a discription and name, gold amount with a number`;
    googleDmTool(
      prompt,
      setLoading,
      setAdventureData,
      setAdventureKeys,
      setQuest,
      apiKey
    ).then(() => {});
  };

  return (
    <>
      {quest ? (
        <>
          {" "}
          <Quest
            x={adventureData}
            z={{
              prompt,
              setLoading,
              setAdventureData,
              setAdventureKeys,
              setQuest,
            }}
          ></Quest>
        </>
      ) : (
        <>
          {loading ? (
            <div id="loading" class="loading-icon"></div>
          ) : (
            <div className="container text-white">
              {" "}
              {/* Set all text white */}
              <h2>Four Text Inputs</h2>
              <form onSubmit={handleSubmit}>
                <label htmlFor="input1">Setting:</label>
                <input
                  type="text"
                  id="input1"
                  name="input1"
                  value={formData.input1}
                  onChange={handleChange}
                  className="form-control text-white bg-dark"
                />

                <label htmlFor="input2">Goal:</label>
                <input
                  type="text"
                  id="input2"
                  name="input2"
                  value={formData.input2}
                  onChange={handleChange}
                  className="form-control text-white bg-dark"
                />

                <label htmlFor="input3">Main Bad:</label>
                <input
                  type="text"
                  id="input3"
                  name="input3"
                  value={formData.input3}
                  onChange={handleChange}
                  className="form-control text-white bg-dark"
                />

                <label htmlFor="input4">Quest Count:</label>
                <input
                  type="text"
                  id="input4"
                  name="input4"
                  value={formData.input4}
                  onChange={handleChange}
                  className="form-control text-white bg-dark"
                />

                <button type="submit" className="btn btn-primary">
                  Submit
                </button>
              </form>
            </div>
          )}
        </>
      )}
    </>
  );
};

export default FourTextInputs;
