import React, { useState, useEffect } from "react";
import Title from "./title";
import OptionsButton from "./OptionsButton";

const OptionsOptions = ({
  player,
  retry,
  section,
  obj,
  keys,
  title,
  isRolled,
  isRolledSetter,
  text,
  setQuestDone,
  setError,
  optionPicked,
  other,
  isGen,
  setIsGen,
}) => {
  const [OOError, setOOError] = useState(false);
  const [outcome, setOutcome] = useState("");
  const [newadventureData, setnewAdventureData] = useState({});
  const [newoptionkeys, setnewOptionKeys] = useState({});
  // console.log();
  try {
    return isRolled ? (
      <>
        <div></div>
      </>
    ) : (
      <div class="row">
        {obj.options.map((item, index) => {
          console.log("item:", item);
          const optionObj = item;
          let skill = optionObj.skill || optionObj.dungeonsAndDragonsSkill;
          if (skill == null) {
            skill = "Stealth";
          }

          console.log("optionsObj", optionObj);

          return (
            <OptionsButton
              setQuestDone={setQuestDone}
              outcome={outcome}
              setOutcome={setOutcome}
              isRolledSetter={isRolledSetter}
              isRolled={isRolled}
              player={player}
              section={section}
              reward={optionObj.reward}
              optionNum={index}
              skill={skill}
              pass={optionObj.pass}
              action={optionObj.action}
              text={optionObj.description || optionObj.text}
              title={title}
              description={text}
              retry={retry}
              other={other}
              isGen={isGen}
              setIsGen={setIsGen}
            ></OptionsButton>
          );
        })}
        <br />
      </div>
    );
  } catch (error) {
    console.log("Retry  at OptionsOptions :", error);
    return (
      <>
        <button onClick={retry}>Retry</button>
      </>
    );
  }
};

export default OptionsOptions;
