import React, { useState } from "react";
import "bootstrap/dist/css/bootstrap.min.css"; // Import Bootstrap CSS
import { googleDmTool } from "./AiGen";
import Quest from "./Quest";
import AdventureQuest from "./AdventureQuest";
import RacePicker from "./RaceClassPicker";

const FourDropdownForm = ({ apiKey }) => {
  console.log(apiKey);
  const [selectedValues, setSelectedValues] = useState({
    dropdown1: "",
    dropdown2: "",
    dropdown3: "",
    dropdown4: "",
  });
  const [loading, setLoading] = useState(false);
  const [quest, setQuest] = useState(false);
  const [selected, setSelected] = useState(false);
  const [adventureData, setAdventureData] = useState({});
  const [adventureKey, setAdventureKeys] = useState([]);

  const handleChange = (event) => {
    const { name, value } = event.target;
    setSelectedValues({ ...selectedValues, [name]: value });
  };

  const settingOptions = [
    { value: "", label: "Select..." },
    { value: "forest", label: "Forrest" },
    { value: "town", label: "Town" },
    { value: "city", label: "City" },
  ];
  const goalOptions = [
    { value: "", label: "Select..." },
    { value: "save the girl", label: "Save the girl" },
    { value: "kill the goblins", label: "Kill the goblins" },
    { value: "make peace", label: "Make Peace" },
  ];
  const bbgOptions = [
    { value: "", label: "Select..." },
    { value: "priest", label: "Priest" },
    { value: "king", label: "King" },
    { value: "dragon", label: "Dragon" },
  ];
  const questCountOptions = [
    { value: "", label: "Select..." },
    { value: "4", label: "4" },
    { value: "5", label: "5" },
    { value: "6", label: "6" },
  ];

  const handleSubmit = (event) => {
    event.preventDefault(); // Prevent default form submission behavior
    console.log("Submitted form data:", selectedValues);
    setLoading(true);
    const prompt = `Reply in pure json format. In this format {message:string,quests:[{description:string,title:string, options:[reward:{type:''description:'',amount:int},fail:{},description:'',pass:int,skill]}]}. You are the gamemaster please form your answer to the following question with these details. The setting is ${selectedValues.dropdown1} with a name. The over all goal is ${selectedValues.dropdown2}. The last quest will face the big bad ${selectedValues.dropdown3}. Give the big bad a name. Please give the character 4 options for interacting with the quests. Each option should have an action and a description in an array. Each option should have a dungeons and dragons skill and no value. Each option should have a 'pass' value between 5 and 15. Each option should have a 'xp' value between 10 and 50. reward should not be 'null'  a 'reward' description as a string is the player is successfull this can be one of the following types as an object; information, an item with a discription and name, gold amount with a number`;
    googleDmTool(
      prompt,
      setLoading,
      setAdventureData,
      setAdventureKeys,
      setQuest,
      apiKey
    ).then(() => {});

    // You can add further logic to submit data to a server or perform other actions here
  };

  return (
    <>
      {quest ? (
        <>
          <AdventureQuest
            x={adventureData}
            z={{
              prompt,
              setLoading,
              setAdventureData,
              setAdventureKeys,
              setQuest,
            }}
          ></AdventureQuest>
        </>
      ) : (
        <>
          <br></br>
          {loading ? (
            <>
              <div id="loading" class="loading-icon"></div>
            </>
          ) : (
            <>
              {selected ? (
                <>
                  <RacePicker setSelected={setSelected}></RacePicker>
                </>
              ) : (
                <>
                  <form onSubmit={handleSubmit}>
                    <h2 className="text-white">Full Quest Gen</h2>

                    <div className="row">
                      <div className="col-md-3 mb-3 ">
                        <label className="text-white" htmlFor="dropdown1">
                          Setting:
                        </label>
                        <select
                          name="dropdown1"
                          id="dropdown1"
                          value={selectedValues.dropdown1}
                          onChange={handleChange}
                          className="form-select"
                        >
                          {settingOptions.map((option) => (
                            <option key={option.value} value={option.value}>
                              {option.label}
                            </option>
                          ))}
                        </select>
                      </div>

                      <div className="col-md-3 mb-3">
                        <label className="text-white" htmlFor="dropdown2">
                          Goal:
                        </label>
                        <select
                          name="dropdown2"
                          id="dropdown2"
                          value={selectedValues.dropdown2}
                          onChange={handleChange}
                          className="form-select"
                        >
                          {goalOptions.map((option) => (
                            <option key={option.value} value={option.value}>
                              {option.label}
                            </option>
                          ))}
                        </select>
                      </div>

                      <div className="col-md-3 mb-3">
                        <label className="text-white" htmlFor="dropdown3">
                          Main Bad Guy:
                        </label>
                        <select
                          name="dropdown3"
                          id="dropdown3"
                          value={selectedValues.dropdown3}
                          onChange={handleChange}
                          className="form-select"
                        >
                          {bbgOptions.map((option) => (
                            <option key={option.value} value={option.value}>
                              {option.label}
                            </option>
                          ))}
                        </select>
                      </div>

                      <div className="col-md-3 mb-3">
                        <label className="text-white" htmlFor="dropdown4">
                          Quest Count:
                        </label>
                        <select
                          name="dropdown4"
                          id="dropdown4"
                          value={selectedValues.dropdown4}
                          onChange={handleChange}
                          className="form-select"
                        >
                          {questCountOptions.map((option) => (
                            <option key={option.value} value={option.value}>
                              {option.label}
                            </option>
                          ))}
                        </select>
                      </div>
                    </div>

                    <br />
                    <button type="submit" className="btn btn-primary">
                      Submit
                    </button>
                  </form>
                </>
              )}
            </>
          )}
        </>
      )}
    </>
  );
};

export default FourDropdownForm;
