import React, { useState } from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import ButtonList from "./OptionsDisplay";
import QuestDisplayAll from "./QuestDisplayAll";
import { googleDmTool } from "./AiGen";
import AdventureSkillCheker from "./AdventureSkillChecker";

const Quest = ({ x, y, z }) => {
  const [currentNum, setCurrentNum] = useState(0);
  const [outcome, setOutcome] = useState("");
  const [done, setDone] = useState(false);
  const [picker, setPicked] = useState(false);
  const [action, setAction] = useState(false);
  let player = JSON.parse(localStorage.getItem("saveFile"));
  let questArray = x.quests;
  let questNum = x.quests.length - 1;
  console.log(questArray);
  const handleSubmit = (event) => {
    event.preventDefault(); // Prevent default form submission behavior
    const prompt = `Reply in pure json format.`;
    if (currentNum !== questNum) {
      setCurrentNum(currentNum + 1);
    } else {
      setDone(true);
    }

    // You can add further logic to submit data to a server or perform other actions here
  };

  return (
    <>
      {done ? (
        <QuestDisplayAll x={questArray}></QuestDisplayAll>
      ) : (
        <>
          <div>
            <br />
            <h2>
              <u>{questArray[currentNum].title.toUpperCase()}</u>
            </h2>
            <br></br>
            <div className="card">
              <div className="card-body">
                <br />
                <h5 className="card-title">
                  <u>{questArray[currentNum].title.toUpperCase()}</u> :
                  {questArray[currentNum].description}
                  <div class="row">
                    {picker ? (
                      <>outcome:{outcome}</>
                    ) : (
                      <>
                        {questArray[currentNum].options.map((item, index) => {
                          console.log("item:", item);
                          const optionObj = item;

                          let skill =
                            optionObj.skill ||
                            optionObj.dungeonsAndDragonsSkill;
                          if (optionObj.reward == null) {
                            console.log("Reward null");
                            optionObj.reward = false;
                          }
                          if (skill == null) {
                            skill = "Stealth";
                          }
                          console.log(" xp", optionObj.xp);
                          console.log("reward : ", optionObj.reward);
                          console.log("optionsObj", optionObj);

                          return (
                            <div class=" col-sm-6  ">
                              {picker ? (
                                <>outcome</>
                              ) : (
                                <div class="card">
                                  <div class="card-body">
                                    <h5 class="card-title">
                                      <u>Option</u>{" "}
                                    </h5>
                                    <p class="card-text">
                                      <br></br>
                                      {optionObj.description}
                                      <br></br>
                                      {optionObj.action}
                                      <br></br>
                                      <b> {optionObj.skill}</b>
                                      <br></br>

                                      <>
                                        reward:{" "}
                                        {optionObj.reward ? (
                                          optionObj.reward.type
                                        ) : (
                                          <> </>
                                        )}
                                        <AdventureSkillCheker
                                          obj={optionObj}
                                          setPicked={setPicked}
                                          action={action}
                                          setAction={setAction}
                                          setOutcome={setOutcome}
                                        ></AdventureSkillCheker>
                                      </>
                                    </p>
                                  </div>
                                </div>
                              )}
                            </div>
                          );
                        })}
                      </>
                    )}
                  </div>
                  <button onClick={handleSubmit}> next</button>
                </h5>
                <br />

                <br />
              </div>
            </div>
            <br />
          </div>
          <nav class="navbar fixed-bottom navbar-light bg-light">
            <a class="navbar-brand" href="#">
              <div className="container ">
                <div className="row  d-flex justify-content-center">
                  <div className="col">Name :{player.name}</div>
                  <div className="col">|</div>
                  <div className="col">Gold:{player.gold}</div>
                  <div className="col">|</div>
                  <div className="col">HP:{player.hp}</div>
                  <div className="col">|</div>
                  <div className="col">XP:{player.hp}</div>
                  <div className="col">|</div>
                </div>
                <div className="row">
                  <div className="col">WIS:{player.wis}</div>
                  <div className="col">DEX:{player.dex}</div>
                  <div className="col">STR:{player.str}</div>
                  <div className="col">CHR:{player.chr}</div>
                  <div className="col">INT:{player.int}</div>
                </div>
                <br></br>
              </div>
            </a>
          </nav>
        </>
      )}
    </>
  );
};

export default Quest;
