import React, { useState } from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import ButtonList from "./OptionsDisplay";
import QuestDisplayAll from "./QuestDisplayAll";
import { googleDmTool } from "./AiGen";

const Quest = ({ x, y, z }) => {
  const [currentNum, setCurrentNum] = useState(0);
  const [done, setDone] = useState(false);
  let questArray = x.quests;
  let questNum = x.quests.length - 1;
  console.log(questArray);
  const handleSubmit = (event) => {
    event.preventDefault(); // Prevent default form submission behavior
    const prompt = `Reply in pure json format.`;
    if (currentNum !== questNum) {
      setCurrentNum(currentNum + 1);
    } else {
      setDone(true);
    }

    // You can add further logic to submit data to a server or perform other actions here
  };

  return (
    <>
      {done ? (
        <QuestDisplayAll x={questArray}></QuestDisplayAll>
      ) : (
        <>
          <div>
            <br />
            <h2>
              <u>{questArray[currentNum].title.toUpperCase()}</u>
            </h2>
            <br></br>
            <div className="card">
              <div className="card-body">
                <br />
                <h5 className="card-title">
                  <u>{questArray[currentNum].title.toUpperCase()}</u> :
                  {questArray[currentNum].description}
                  <div class="row">
                    {questArray[currentNum].options.map((item, index) => {
                      console.log("item:", item);
                      const optionObj = item;

                      let skill =
                        optionObj.skill || optionObj.dungeonsAndDragonsSkill;
                      if (optionObj.reward == null) {
                        console.log("Reward null");
                        optionObj.reward = false;
                      }
                      if (skill == null) {
                        skill = "Stealth";
                      }
                      console.log(" xp", optionObj.xp);
                      console.log("reward : ", optionObj.reward);
                      console.log("optionsObj", optionObj);

                      return (
                        <div class=" col-sm-6  ">
                          {false ? (
                            <>outcome</>
                          ) : (
                            <div class="card">
                              <div class="card-body">
                                <h5 class="card-title">
                                  <u>Option</u>{" "}
                                </h5>
                                <p class="card-text">
                                  <br></br>
                                  {optionObj.description}
                                  <br></br>
                                  {optionObj.action}
                                  <br></br>
                                  <b> {optionObj.skill}</b>
                                  <br></br>

                                  <>
                                    reward:{" "}
                                    {optionObj.reward ? (
                                      optionObj.reward.type
                                    ) : (
                                      <> </>
                                    )}
                                  </>
                                </p>
                              </div>
                            </div>
                          )}
                        </div>
                      );
                    })}
                  </div>
                  <button onClick={handleSubmit}> next</button>
                </h5>
                <br />

                <br />
              </div>
            </div>
            <br />
          </div>
        </>
      )}
    </>
  );
};

export default Quest;
