export async function googleApi(
  prompt,
  setIsGen,
  setError,
  setApiButtonIsLoading,
  setAdventureData,
  setAdventureKeys,
  setAdventureStarted,
  apiKey
) {
  console.log(prompt);
  setIsGen(true);
  setError(false);
  setApiButtonIsLoading(true);
  const genAI = new GoogleGenerativeAI(apiKey);
  const model = genAI.getGenerativeModel({ model: "gemini-1.0-pro" });
  var div = document.getElementById("response");
  const result = await model.generateContent(prompt);
  const response = await result.response;
  console.log(response.text());
  let adventureObj;

  //----------------google ai ------------------------------
  try {
    adventureObj = JSON.parse(response.text());
    div.innerHTML = adventureObj.message;
    console.log("adventureObj :", adventureObj);
    setAdventureData(adventureObj);
    let keyArray = [];

    const keys = Object.keys(adventureObj);
    keys.forEach(function eachKey(key) {
      console.log("key :", key);
      if (key === "quests") {
        console.log("Has children :", key);
        keyArray.push(key);
      }
    });

    setAdventureKeys(keyArray);
    setAdventureStarted(true);
    setApiButtonIsLoading(false);
    return adventureObj;
  } catch (error) {
    console.log("error :", error);
    console.log("Trys :", trys);
    trys = trys + 1;
    if (trys < 40) {
      googleApi(
        prompt,
        setIsGen,
        setError,
        setApiButtonIsLoading,
        setAdventureData,
        setAdventureKeys,
        setAdventureStarted,
        apiKey
      );
    }
  }
}

export async function googleDm(
  prompt,
  setIsGen,
  setError,
  setOptionsDMIsLoading,
  setAdventureData,
  setAdventureKeys,
  setOptionPicked,
  apiKey
) {
  console.log(prompt);
  setIsGen(true);
  setError(false);
  setOptionsDMIsLoading(true);
  const genAI = new GoogleGenerativeAI(apiKey);
  const model = genAI.getGenerativeModel({ model: "gemini-1.0-pro" });
  const result = await model.generateContent(prompt);
  const response = await result.response;
  console.log(response.text());
  let adventureObj;

  //----------------google ai ------------------------------
  try {
    adventureObj = JSON.parse(response.text());
    console.log("adventureObj :", adventureObj);
    setAdventureData(adventureObj);
    setOptionsDMIsLoading(false);
    setOptionPicked(true);
  } catch (error) {
    try {
      setAdventureData(
        JSON.parse(response.text().split("json")[1].split("`")[0])
      );
      setOptionsDMIsLoading(false);
      setOptionPicked(true);
    } catch (error) {
      console.log("error :", error);
      console.log("Trys :", trys);
      trys = trys + 1;
      if (trys < 40) {
        googleDm(
          prompt,
          setIsGen,
          setError,
          setOptionsDMIsLoading,
          setAdventureData,
          setAdventureKeys,
          setOptionPicked,
          apiKey
        );
      }
    }
    setOptionsDMIsLoading(false);
    setOptionPicked(true);
    return adventureObj;
  }
}

export async function ollama(
  prompt,
  setIsGen,
  setError,
  setApiButtonIsLoading,
  setAdventureData,
  setAdventureKeys
) {
  console.log(prompt);
  setIsGen(true);
  setError(false);
  setApiButtonIsLoading(true);
  // const genAI = new GoogleGenerativeAI(process.env.REACT_APP_API);
  // const model = genAI.getGenerativeModel({ model: "gemini-1.0-pro" });
  var div = document.getElementById("response");
  const response = await fetch(`${process.env.REACT_APP_NGROK}/api/chat`, {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify({
      model: "phi3",
      messages: [{ role: "user", content: prompt }],
      stream: false,
    }),
  }).json();
  console.log(response);
  let adventureObj;

  //----------------google ai ------------------------------
  try {
    adventureObj = JSON.parse(response.text());
    div.innerHTML = adventureObj.message;
    console.log("adventureObj :", setAdventureData);
    setAdventureData(adventureObj);
    let keyArray = [];
    try {
      const keys = Object.keys(adventureObj);
      keys.forEach(function eachKey(key) {
        // console.log(key, " : ",adventureObj[key])

        try {
          if (
            adventureObj[key][0].description ||
            adventureObj[key][0].title ||
            adventureObj[key][0].destination ||
            adventureObj[key][0].innkeeper.welcome
          ) {
            // console.log("Has children :", key)
            keyArray.push(key);
          }
        } catch {}
      });
    } catch {}
    setAdventureKeys(keyArray);
  } catch (error) {
    console.log("error :", error);
    console.log("Trys :", trys);
    trys = trys + 1;
    if (trys < 40) {
      googleDm(prompt, setIsGen, setError, setApiButtonIsLoading);
    }
  }

  return adventureObj;
}

export async function googleDmTool(
  prompt,
  loading,
  setAdventureData,
  setKeys,
  setQuest,
  apiKey
) {
  loading(true);
  const genAI = new GoogleGenerativeAI(apiKey);
  const model = genAI.getGenerativeModel({ model: "gemini-1.0-pro" });
  const result = await model.generateContent(prompt);
  const response = await result.response;
  console.log(response.text());
  let adventureObj;
  var div = document.getElementById("response");

  try {
    adventureObj = JSON.parse(response.text());
    div.innerHTML = adventureObj.message;
    console.log("adventureObj :", adventureObj);
    setAdventureData(adventureObj);
    let keyArray = [];

    const keys = Object.keys(adventureObj);
    keys.forEach(function eachKey(key) {
      console.log("key :", key);
      if (key === "quests") {
        console.log("Has children :", key);
        keyArray.push(key);
      }
    });

    setKeys(keyArray);
    loading(false);
    setQuest(true);
    return adventureObj;
  } catch (error) {
    console.log(error);
    try {
      div.innerHTML = adventureObj.message;
      setAdventureData(
        JSON.parse(response.text().split("json")[1].split("`")[0])
      );

      adventureObj = JSON.parse(response.text().split("json")[1].split("`")[0]);
      const reward = adventureObj.quests[0].reward;
      console.log("adventureObj :", reward.type);
      loading(false);
      setQuest(true);
    } catch (error) {
      console.log("error :", error);
      console.log("Trys :", trys);
      trys = trys + 1;
      if (trys < 40) {
        googleDmTool(
          prompt,
          loading,
          setAdventureData,
          setKeys,
          setQuest,
          apiKey
        );
      }
    }
  }
}
