import React, { useState } from "react";
import OptionTitle from "./OptionTitle";
import DmTool from "./DmTool";
import TextInputTool from "./TextInputTool";
import ApiButton from "./ApiButton";
import Adventure from "./Adventure";
import Start from "./Start";

const ToolPicker = ({ apiKey }) => {
  const [dropdown, setDropdown] = useState(false);
  const [text, setText] = useState(false);
  const [picked, setPicked] = useState(false);
  const [adventure, setAdventure] = useState(false);
  return (
    <>
      {picked ? (
        <>
          <button
            onClick={() => {
              setPicked(false);
              setDropdown(false);
              setText(false);
              setAdventure(false);
            }}
            className="btn btn-primary col-12"
          >
            {" "}
            BACK
          </button>
        </>
      ) : (
        <>
          {" "}
          <button
            className="btn btn-primary"
            onClick={() => {
              setText(true);
              setPicked(true);
            }}
          >
            Text
          </button>
          <div className="row">
            <div className="col-6">
              <div>OR</div>
            </div>
          </div>{" "}
          <button
            className="btn btn-primary"
            onClick={() => {
              setDropdown(true);
              setPicked(true);
            }}
          >
            Dropdown
          </button>
          <div>OR</div>
          <div className="row">
            <button
              className="btn btn-primary"
              onClick={() => {
                setAdventure(true);
                setPicked(true);
              }}
            >
              Adventure
            </button>
          </div>
        </>
      )}
      <div className="row">
        {text ? (
          <>
            <TextInputTool apiKey={apiKey}></TextInputTool>
          </>
        ) : (
          <></>
        )}

        {dropdown ? (
          <>
            <DmTool apiKey={apiKey}></DmTool>
          </>
        ) : (
          <></>
        )}

        {adventure ? (
          <>
            <Start apiKey={apiKey}></Start>
          </>
        ) : (
          <></>
        )}
      </div>
    </>
  );
};

export default ToolPicker;
