import React, { useState } from "react";
import "bootstrap/dist/css/bootstrap.min.css"; // Import Bootstrap CSS
import DmTool from "./DmTool";
import ToolPicker from "./ToolPicker";

const LoginComponent = () => {
  const [login, setLogin] = useState(false);
  const [apiKey, setApiKey] = useState("");
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [errorMessage, setErrorMessage] = useState("");

  const handleSubmit = (event) => {
    event.preventDefault(); // Prevent default form submission

    // Replace with your actual login logic (e.g., sending data to a server)
    setUsername();
    let envString = `REACT_APP_${username.toUpperCase()}_PASS`;
    let correctPass = process.env[envString];
    if (password === correctPass) {
      // Login successful
      const apiEnv = `REACT_APP_${username.toUpperCase()}_API`;
      setApiKey(process.env[apiEnv]);
      setLogin(true);
      // Redirect to another page or perform other actions (use appropriate routing library)
    } else {
      // Login failed
      console.log("errr");
    }
  };

  return (
    <>
      {login ? (
        <>
          <ToolPicker apiKey={apiKey}></ToolPicker>
        </>
      ) : (
        <>
          <div className="login-container text-white">
            {" "}
            {/* Set all text white */}
            <h1>Login</h1>
            <form onSubmit={handleSubmit}>
              <label htmlFor="username" className="text-white">
                Username:
              </label>
              <input
                type="text"
                id="username"
                name="username"
                value={username}
                onChange={(e) => setUsername(e.target.value)}
                required
                className="form-control text-white bg-dark"
              />
              <label htmlFor="password" className="text-white">
                Password:
              </label>
              <input
                type="password"
                id="password"
                name="password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                required
                className="form-control text-white bg-dark"
              />
              <button type="submit" className="btn btn-primary">
                Log In
              </button>
            </form>
          </div>
        </>
      )}
    </>
  );
};

export default LoginComponent;
